import styled from "@emotion/styled";

interface ExploreItemVideoProps {
  isSpecial: boolean;
}

const ExploreList = styled.ul`
  display: block;
  clear: both;
`;

const ExploreItemVideoWrap = styled.li<ExploreItemVideoProps>`
  float: left;
  width: calc(33.3333% - 4px);
  margin: 0 4px 4px 0;
  aspect-ratio: 1/2;
  background-color: #eee;
  cursor: pointer;

  ${({ isSpecial }) =>
    isSpecial &&
    `float: right
  `}
`;

const ExploreItemVideo = styled.div`
  height: calc(100% + 4px);
`;

const ExploreItemImg = styled.li`
  float: left;
  margin: 0 4px 4px 0;
  width: calc(33.3333% - 4px);
  aspect-ratio: 1/1;
  background-color: #eee;
  cursor: pointer;
`;

function createPattern(isVideoFirst: boolean) {
  const pattern: JSX.Element[] = [];
  if (isVideoFirst) {
    pattern.push(
      <ExploreItemVideoWrap key="video" isSpecial={true}>
        <ExploreItemVideo>Video</ExploreItemVideo>
      </ExploreItemVideoWrap>
    );
    for (let i = 0; i < 4; i++) {
      pattern.push(<ExploreItemImg key={`image-${i}`}>Image</ExploreItemImg>);
    }
  } else {
    pattern.push(
      <ExploreItemVideoWrap key="video" isSpecial={false}>
        <ExploreItemVideo>Video</ExploreItemVideo>
      </ExploreItemVideoWrap>
    );
    for (let i = 0; i < 4; i++) {
      pattern.push(<ExploreItemImg key={`image-${i}`}>Image</ExploreItemImg>);
    }
  }
  return pattern;
}

function Explore() {
  const allPatterns: JSX.Element[] = [];

  for (let i = 0; i < 20; i++) {
    const isVideoFirst = i % 2 === 0; // 짝수/홀수 패턴 선택
    const pattern = createPattern(isVideoFirst);
    allPatterns.push(<ExploreList key={i}>{pattern}</ExploreList>);
  }

  return <>{allPatterns}</>;
}

export default Explore;
