import { useRecoilState } from "recoil";
import { appState } from "../../recoil/atoms/DynamicContents";

import Lnb from "./Lnb";
import Profile from "./Profile";
import ProfileStory from "./ProfileStory";
import Switch from "./SwitchButton";
import Post from "./Post";
import Search from "./Search";
import Explore from "./Explore";

import styled from "@emotion/styled";

// 스위치 랩퍼 스타일 컴포넌트 정의
const ContentsWrap = styled.div`
  width: calc(100% - var(--nav-narrow-width));
  margin-left: auto;
  padding: 30px 20px 0;
`;
function Contents() {
  const [state] = useRecoilState(appState);

  return (
    <>
      <Lnb />
      <ContentsWrap>
        <Search show={state.isSearch === true} />
        {state.activeTab === "home" && (
          <>
            <Profile />
            <ProfileStory />
            <Switch />
            <Post />
          </>
        )}
        {state.activeTab === "explore" && <Explore />}
        {state.activeTab === "reels" && <div>reels</div>}
        {state.activeTab === "direction" && <div>direction</div>}
        {state.activeTab === "post" && <div>post</div>}
        {state.activeTab === "alarm" && <div>alarm</div>}
      </ContentsWrap>
    </>
  );
}
export default Contents;
